<template>
  <div class="ballRolling" :style="style">
    <svg width="100%" height="100%" xmlns="http://www.w3.org/2000/svg">
      <defs>
        <linearGradient x1="0%" y1="0%" x2="100%" y2="100%" id="gradient1">
          <stop
            offset="0%"
            style="stop-color: rgb(124, 244, 194); stop-opacity: 1"
          />
          <stop
            offset="100%"
            style="stop-color: rgb(124, 174, 244); stop-opacity: 1"
          />
        </linearGradient>
      </defs>
      <rect
        width="100%"
        height="100%"
        fill="none"
        :stroke-width="strokeWidth"
        stroke="#eee"
      ></rect>
      <rect
        width="100%"
        height="100%"
        fill="none"
        :stroke-width="strokeWidth"
        stroke="url(#gradient1)"
        :stroke-dasharray="dasharray"
        :stroke-dashoffset="batterypercent"
      ></rect>
    </svg>
  </div>
</template>
<script setup>
import { ref, onMounted, computed } from "vue";
const props = defineProps({
  style: {
    type: Object,
    default: () => {
      return {
        width: "300px",
        height: "300px",
      };
    },
  },
  strokeWidth: {
    type: Number,
    default: 20,
  },
  rollLength: {
    type: Number,
    default: 10,
  },
  time: {
    type: Number,
    default: 1000,
  },
});

const percent = ref(0);
onMounted(() => {
  play();
});
const batterypercent = computed(() => {
  return 300 * 4 - (300 * 4 * percent.value) / 100;
});
const dasharray = computed(() => {
  let width = parseFloat(props.style.width);
  let height = parseFloat(props.style.height);
  let perimeter = width * 2 + height * 2;
  return `${props.rollLength} ${perimeter - props.rollLength}`;
});
const play = () => {
  setTimeout(() => {
    if (percent.value > 100) {
      percent.value = 0;
    }
    percent.value += 2;
    play();
  }, props.time);
};
</script>
<style lang="scss" scoped>
.ballRolling {
}
</style>